.PaDbody{
    background-color: white;
    display: flex;
    justify-content: center;
}

.PaDcontainer{
    max-width: 1200px;
    width: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    margin-top: 80px;
}

.PaDcontainer > div {
    display: flex;
    flex-direction: column;
    margin: 50px 20px;
}

.PaDcontainer > div > h2 {
    margin: 50px 0px;
}

.PaDcontainer > div > ul > li {
    margin: 20px 0px;
    width: 100%;
}

.PaDhead{
    display: flex;
    flex-direction: column;
    align-self: center;
}


.PaDparagraf {
    width: 100%;
    display: flex;
    text-align: left;
    align-items: flex-start;
}

@media only screen and (max-width: 760px) {
    .PaDcontainer {
        margin-top: 80px;
    }
}